.card{
    width: 100vw;
    height: auto;
    overflow: hidden;
}
.meal-content{
    position: relative;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size:100%;
    height:auto;
    display:flex;
    flex-direction: column;

    color:white;
    overflow: hidden;
    transition: 0.5s;
}
.meal-content:hover{
    background-size: 110%;
    overflow: hidden;
}
.text-content{
    overflow: hidden;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.3);
    height: 200px;
    width: 100%;
}
.text-content p{
    opacity: 0;
    transition: .5s;
}
.meal-content:hover .text-content p{
    opacity: 1;
}
.text-border{
    background: rgba(0, 0, 0, 0.3);
    border:3px solid white;
    padding: 5px;
    width: 200px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
.text-border:before{
    content:"";
    border:1px solid white;
    position: absolute;
    top:5px;
    left:5px;
    right:5px;
    bottom: 5px;
}

@media only screen and (min-width: 700px) {
    .card{
        width: 49.32vw;
    }
  }
  @media only screen and (max-width: 700px) {
    .text-content p{
        opacity: 1;
    }
  }

